.case__numbers {
  font-family: @add-font;
  font-size: 24px;
  line-height: 1;
  color: @black-color;

  @media (min-width: @tablet-width) {
    font-size: 30px;
    line-height: 37px;
  }
}

.case__meaning {
  font-size: 12px;
  line-height: 1;
  margin: 0;

  @media (min-width: @tablet-width) {
    font-size: 14px;
  }
}

.case__costs-wrapper {
  font-family: @add-font;
  font-size: 14px;
  line-height: 20px;
  color: @black-color;
  text-transform: uppercase;
  display: flex;

  @media (min-width: @tablet-width) {
    font-size: 20px;
    line-height: 1;
  }

  @media (min-width: @desktop-width) {
    justify-content: space-between;
  }
}

.case__costs-meaning {
  margin: 0;
}

.case__title {
  padding-top: 25px;
  padding-bottom: 40px;
  font-size: 36px;
  line-height: 1;

  @media (min-width: @tablet-width) {
    padding-top: 45px;
    padding-bottom: 60px;
    font-size: 60px;
    line-height: 1;
  }

  @media (min-width: @desktop-width) {
    padding-top: 69px;
    padding-bottom: 76px;
  }
}

.case__description {
  margin: 0;

  @media (min-width: @tablet-width) {
    font-size: 16px;
    line-height: 24px;
  }
}

.case__list {
  margin: 0;
  display: flex;
  justify-content: space-between;
}

.case__container {
  @media (min-width: @tablet-width) {
    display: flex;
    justify-content: space-between;
  }

  @media (min-width: @desktop-width) {
    flex-direction: column;
  }
}
